@import "core/core-mixins";

/* -------------------------------------------------------------- *\
    Utility Variables
\* -------------------------------------------------------------- */

$utility-baseUnit                                    : 6px;
$utility-baseUnitHalf                                : $utility-baseUnit / 2; // Derived
$utility-baseUnitDouble                              : $utility-baseUnit * 2; // Derived
$utility-percentage_third                            : (100% / 3);
$utility-SVGEncoding                                 : "data:image/svg+xml,";

/* -------------------------------------------------------------- *\
    Globals
\* -------------------------------------------------------------- */

$global-color_black                                  : #000;
$global-color_white                                  : #fff;
$global-color_transparent                            : transparent;
$global-color_fg                                     : #555a62 !default;
$global-color_bg                                     : #fff !default;
$global-color_primary                                : #0291db !default;
$global-color_primarySubtle                          : rgba($global-color_primary, .02) !default; // Derived
$global-color_secondary                              : darken($global-color_primary, 10%) !default; // Derived
$global-color_warning                                : #ffce00 !default;

$global-error_fg                                     : #ff3933 !default;
$global-error_bg                                     : mix($global-error_fg, $global-color_bg, 10%) !default; // Derived

$global-color_confirm                                : #60bd68 !default;
$global-color_unresolved                             : mix($global-color_warning, $global-color_fg, 10%) !default; // Resolved

$global-body_bg                                      : $global-color_bg !default; // Derived - color outside main content in margins

$global-border_color                                  : mix($global-color_warning, $global-color_fg, 50%) !default; // Note it's off by default due to the border width
$global-border-width                                  : 0;
$global-border_radius                                 : 2px !default;

/* -------------------------------------------------------------- *\
    Password Strength Colors
\* -------------------------------------------------------------- */

$passwordStrength-level1_color                       : #c81818 !default;
$passwordStrength-level2_color                       : #ff891d !default;
$passwordStrength-level3_color                       : #ffac1d !default;
$passwordStrength-level4_color                       : #5DB112 !default;
$passwordStrength-level5_color                       : #27b30f !default;
$passwordStrength-bg                                 : mix($global-error_bg, $global-error_fg, 80%) !default; // Derived

/* -------------------------------------------------------------- *\
    Third Party Colors
\* -------------------------------------------------------------- */

$global-color_facebook                               : #3b5998;
$global-color_twitter                                : #00b6f1;
$global-color_youTube                                : #b31217;
$global-color_pinterest                              : #cb2027;
$global-color_googlePlus                             : #df4a32;
$global-color_tumblr                                 : #2c4762;
$global-color_stumbleUpon                            : #eb4924;
$global-color_reddit                                 : #ff5700;
$global-color_vine                                   : #00b489;
$global-color_discord                                : #7289da;
$global-color_instagram                              : #bc2a8d;
$global-color_linkedIn                               : #007bb6;

/* -------------------------------------------------------------- *\
    Typography
\* -------------------------------------------------------------- */

$global-title_fontSize                                 : 30px !default;
$global-subTitle_fontSize                              : 18px !default;

$global-large_fontSize                                 : 16px !default;
$global-medium_fontSize                                : 14px !default;
$global-small_fontSize                                 : 12px !default;

$global-normal_fontWeight                              : 400 !default;
$global-bold_fontWeight                                : 600 !default;

$global-lineHeight_offset                              : .32em !default; // Offset font to make top of capital letters flush with top of container

$global-base_lineHeight                                : (20/14) !default;

$global-heading_fontFamily                             : "Open Sans", HelveticaNeue-Light, "Segoe UI", "Helvetica Neue", Helvetica, Raleway, Arial, sans-serif !default;
$global-heading_fontWeight                             : $global-bold_fontWeight !default; // Derived

$global-body_fontFamily                                : "Open Sans", "Segoe UI", "Helvetica Neue", Helvetica, Raleway, Arial, sans-serif !default;
$global-body_fontWeight                                : $global-normal_fontWeight !default; // Derived

$link_default-color                                    : $global-color_primary;
$link_default_hover-color                              : $global-color_secondary;
$link_default_focus-color                              : $global-color_secondary;

/* -------------------------------------------------------------- *\
    Animation
\* -------------------------------------------------------------- */

$global-default_timing                               : .15s !default;
$global-default_easing                               : ease-out !default;

/* -------------------------------------------------------------- *\
    Icons
\* -------------------------------------------------------------- */

$icon-default_size                                   : 24px !default;
$icon-large_size                                     : 32px !default;


/* -------------------------------------------------------------- *\
    Layout
\* -------------------------------------------------------------- */

$global-gutter_size                                  : $utility-baseUnit * 4;
$global-gutter_halfSize                              : $global-gutter_size / 2;
$global-gutter_quarterSize                           : $global-gutter_size / 4;
$global-main_width                                   : 1124px !default;
$global-main_paddedWidth                             : $global-main_width + 2 * $global-gutter_size !default;  // Derived
$global-panel_width                                  : 226px;
$global-panelGutter_width                            : 36px;

$global-twoColumn_breakpoint                         : 650px; // Generic breakpoint for 2 columns

$panelAndNav-twoColumn_breakpoint                    : $global-main_paddedWidth;
$panelAndNav-oneColumn_breakpoint                    : $global-twoColumn_breakpoint;

// Hard coded columns
$evenColumns_threeColumns_breakToOne                 : $global-twoColumn_breakpoint;

// Uses CSS Columns
$flexColumns-twoColumns_breakToOne                   : $global-twoColumn_breakpoint;
$flexColumns-threeColumns_breakToTwo                 : $global-twoColumn_breakpoint;
$flexColumns-threeColumns_breakToOne                 : 500px;

/* -------------------------------------------------------------- *\
    Components
\* -------------------------------------------------------------- */

$component_bg                                        : mix($global-color_bg, $global-color_fg, 97%) !default;
$component-titleBar_bg                               : mix($global-color_bg, $global-color_fg, 93%) !default;
$component-body_bg                                   : $component_bg !default;
$component-well_bg                                   : $global-color_bg !default;

$component-separator                                 : $utility-baseUnit * 2;
$component-spacing                                   : 27px;
$component-spacing_half                              : $component-spacing / 2;
$component-viewAll_bg                                : $component_bg !default;

$component-titleBar_verticalPadding                  : $component-spacing_half;
$component-titleBar_minHeight                        : 60px;
$component-titleBar_countBg                          : mix($global-color_bg, $global-color_fg, 88%) !default;
$component-titleBar_viewMoreColor                    : mix($global-color_bg, $global-color_fg, 60%) !default;


/* -------------------------------------------------------------- *\
    Widgets
\* -------------------------------------------------------------- */

// Global
$global-widget_outerPadding                          : $global-gutter_quarterSize;
$global-widget_innerPadding                          : $global-gutter_quarterSize;
$global-widgetSection-gap                            : 0; // Between widget sections
$global-widget_gap                                   : $global-gutter_size; // Between widgets
$global-widget_bg                                    : transparent;
$global-widget_fg                                    : $global-color_fg;

$global-widgetBorder_color                           : $global-border_color;
$global-widgetBorder_radius                          : $global-border-radius;

$global-widgetTitle_size                             : $global-subTitle_fontSize;
$global-widgetSubTitle_size                          : $global-large_fontSize;
$global-widgetText_size                              : $global-medium_fontSize;


// Outer Widget Wrap
$widget-top_padding                                  : $global-widget_outerPadding;
$widget-bottom_padding                               : $global-widget_outerPadding;
$widget-left_padding                                 : $global-widget_outerPadding;
$widget-right_padding                                : $global-widget_outerPadding;
$widget_borderColor                                  : $global-widgetBorder_color;
$widget_borderWidth                                  : 0;
$widget_borderRadius                                 : $global-widgetBorder_radius;

// Inner section
$widget-section_topPadding                           : $global-widget_innerPadding;
$widget-section_bottomPadding                        : $global-widget_innerPadding;
$widget-section_leftPadding                          : $global-widget_innerPadding;
$widget-section_rightPadding                         : $global-widget_innerPadding;

// Widget Contents - no padding on this guy, comes from wrapper or sub sections
$widget-contents_bg                                  : $global-widget_bg;

// Widget Head
$widget-head_topPadding                              : $widget-section_topPadding;
$widget-head_rightPadding                            : $widget-section_rightPadding;
$widget-head_bottomPadding                           : $widget-section_bottomPadding;
$widget-head_leftPadding                             : $widget-section_leftPadding;
$widget-head_bg                                      : $global-widget_bg;
$widget-head_fg                                      : $global-widget_fg;
$widget-head_gap                                     : $global-widgetSection-gap;  // Bottom Margin
$widget-head_borderColor                             : $global-widgetBorder_color;
$widget-head_borderWidth                             : 0;
$widget-head_borderRadius                            : $global-widgetBorder_radius;


// "Main" (body and footer)
$widget-main_bg                                      : transparent;
$widget-main_fg                                      : transparent;
$widget-main_borderColor                             : $global-widgetBorder_color;
$widget-main_borderWidth                             : 0;
$widget-main_borderRadius                            : $global-widgetBorder_radius;


// Widget Body
$widget-body_topPadding                              : $widget-section_topPadding;
$widget-body_bottomPadding                           : $widget-section_bottomPadding;
$widget-body_leftPadding                             : $widget-section_leftPadding;
$widget-body_rightPadding                            : $widget-section_rightPadding;
$widget-body_bg                                      : transparent;
$widget-body_fg                                      : $global-widget_fg;
$widget-body_borderColor                             : $global-widgetBorder_color;
$widget-body_borderWidth                             : 0;
$widget-body_borderRadius                            : $global-widgetBorder_radius;

// Widget Footer
$widget-footer_topPadding                            : $widget-section_topPadding;
$widget-footer_bottomPadding                         : $widget-section_bottomPadding;
$widget-footer_leftPadding                           : $widget-section_leftPadding;
$widget-footer_rightPadding                          : $widget-section_rightPadding;
$widget-footer_bg                                    : transparent;
$widget-footer_fg                                    : $global-widget_fg;
$widget-footer_gap                                   : $global-widgetSection-gap; // Top Margin
$widget-footer_borderColor                           : $global-widgetBorder_color;
$widget-footer_borderWidth                           : 0;
$widget-footer_borderRadius                          : $global-widgetBorder_radius;

/* -------------------------------------------------------------- *\
    Form Elements
\* -------------------------------------------------------------- */

$formElement_height                                  : $utility-baseUnit * 6 !default; // Derived
$formElement_borderWidth                             : 1px !default;
$formElement_borderColor                             : mix($global-color_fg, $global-color_bg, 24%) !default; // Derived
$formElement_borderRadius                            : $global-border_radius !default;
$formElement_giantInput_height                       : 51px !default;
$formElement_giantInput_fontSize                     : $utility-baseUnit * 4 !default; // Derived
$formElement_halfHeight                              : $formElement_height / 2 !default; // Derived
$formElement-border_widthFull                        : $formElement_borderWidth * 2; // Derived
$formElement_horizontalPadding                       : $utility-baseUnit * 2 !default; // Derived
$formElement_paddingVertical                         : $utility-baseUnit !default; // Derived
$formElement_placeholderColor                        : mix($global-color_fg, $global-color_bg, 50%) !default; // Derived
$formElement_maxWidth                                : 528px !default;
$formElement_fg                                      : mix($global-color_fg, $global-color_bg, 80%) !default; // Derived
$formElement_bg                                      : $global-color_bg !default; // Derived
$global-formNote_fg                                  : mix($global-color_fg, $global-color_bg, 72%) !default; // Derived
$global-formNoteLink_fg                              : $global-color_primary !default; // Derived

$global-selectBox_height                             : $utility-baseUnit * 4 !default; // Derived

$global-textArea_minHeight                           : 100px;
$global-textArea_padding                             : $utility-baseUnit;

$global-fileUpload_bg                                : $global-color_primary;
$global-fileUpload_fg                                : $global-color_white;
$global-fileUpload_hover_bg                          : mix($global-color_primary, $global-color_fg, 20%);



/* -------------------------------------------------------------- *\
    Buttons
\* -------------------------------------------------------------- */

// Global Button options

$global-button_paddingTop                            : 2px !default;
$global-button_paddingBottom                         : 3px !default;
$global-button_paddingSide                           : $utility-baseUnit !default;
$global-button_minWidth                              : 16 * $utility-baseUnit !default; // Derived
$global-button_radius                                : $global-border_radius;

$buttonBar_margin                                    : $utility-baseUnit * 2; // Derived

// Basic Button

$button-basic_fg                                     : $global-color_fg !default;
$button-basic_bg                                     : $global-color_bg !default;
$button-basic_border                                 : mix($global-color_fg, $global-color_bg, 24%) !default;

$button-basic_hover_fg                               : $button-basic_fg !default;
$button-basic_hover_bg                               : $global-color_bg !default;
$button-basic_hover_border                           : mix($global-color_bg, $global-color_fg, 40%) !default;

$button-basic_focus_fg                               : $global-color_fg !default;
$button-basic_focus_bg                               : $button-basic_hover_bg !default;
$button-basic_focus_border                           : rgba($global-color_primary, .8) !default;

$button-basic: (
    default: (
        color: $button-basic_fg,
        background-color: $button-basic_bg,
        border-color: $button-basic_border
    ),
    hover: (
        color: $button-basic_hover_fg,
        background-color: $button-basic_hover_bg,
        border-color: $button-basic_hover_border
    ),
    focus: (
        color: $button-basic_focus_fg,
        background-color: $button-basic_focus_bg,
        border-color: $button-basic_focus_border
    )
);


// Button Primary

$button-primary_fg                                   : $global-color_white !default;
$button-primary_bg                                   : $global-color_primary !default;
$button-primary_border                               : $global-color_primary !default;

$button-primary_hover_fg                             : $global-color_white !default;
$button-primary_hover_bg                             : $global-color_secondary !default;
$button-primary_hover_border                         : $global-color_secondary !default;

$button-primary_focus_fg                             : $global-color_white !default;
$button-primary_focus_bg                             : $global-color_secondary !default;
$button-primary_focus_border                         : $global-color_secondary !default;

$buttonBranded: (
    default: (
        color: $button-primary_fg,
        background-color: $button-primary_bg,
        border-color: $button-primary_border
    ),
    hover: (
        color: $button-primary_hover_fg,
        background-color: $button-primary_hover_bg,
        border-color: $button-primary_hover_border
    ),
    focus: (
        color: $button-primary_focus_fg,
        background-color: $button-primary_focus_bg,
        border-color: $button-primary_focus_border
    )
);



/* -------------------------------------------------------------- *\

\* -------------------------------------------------------------- */



